.footer_form {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.form_input {
  width: 200px;
  height: 25px;
  padding: 5px;
  border: 3px solid #ccc;
  transition: 0.5s;
  &:focus {
    border: 3px solid  #F6B352;
  }
  @media all and (min-width: 740px){
    width: 400px;
  }
  @media all and (min-width: 980px) {
    width: 500px;
  }
}

.form_button {
  height: 25px;
  padding: 5px;
  border: none;
  color: white;
  background-color: #1F2124;
  cursor: pointer;
  transition: 1s;
  &:hover {
    background-color: #30A9DE;
  }
}