@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,300i,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto; }

.header_container {
  width: 100%;
  height: 100px;
  background-color: #383A3F;
  display: flex;
  justify-content: center;
  align-items: center; }

.header_title {
  text-align: center;
  padding: 10px 0 10px 0;
  color: white; }

.body_container {
  width: 100%;
  background-color: #383A3F;
  background-image: url(../../static/media/body_background.5ceb1c93.jpg);
  background-position: center center;
  background-size: cover; }

.notes_body {
  background-color: white;
  height: calc(100vh - 170px); }

.footer_container {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  background-color: #383A3F; }

.notes_footer {
  padding: 10px 0 10px 0; }

.footer_form {
  display: flex;
  align-items: flex-end;
  justify-content: center; }

.form_input {
  width: 200px;
  height: 25px;
  padding: 5px;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s; }
  .form_input:focus {
    border: 3px solid  #F6B352; }
  @media all and (min-width: 740px) {
    .form_input {
      width: 400px; } }
  @media all and (min-width: 980px) {
    .form_input {
      width: 500px; } }

.form_button {
  height: 25px;
  padding: 5px;
  border: none;
  color: white;
  background-color: #1F2124;
  cursor: pointer;
  -webkit-transition: 1s;
  transition: 1s; }
  .form_button:hover {
    background-color: #30A9DE; }

.body_note {
  display: flex;
  align-items: center;
  padding: 0 20px 10px 20px;
  font-size: 20px; }

.notes_body {
  overflow: auto; }

.note_content {
  width: 100%; }

.note_button {
  font-weight: 700;
  font-size: 30px;
  -webkit-transition: 0.5s;
  transition: 0.5s; }
  .note_button:hover {
    color: crimson;
    cursor: pointer; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  color: #343838;
  font-family: 'Open Sans Condensed', sans-serif; }

